/* Define color variables for easy configuration */
:root {
  --primary-blue: #02081d; /* Deep blue */
  --light-blue: #DBEAFE; /* Light blue background */
  --white: #FFFFFF; /* Pure white */
  --glass-bg: rgba(255, 255, 255, 0.15); /* Glass effect background */
  --glass-border: rgba(255, 255, 255, 0.3);
  --shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

/* Reset default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--light-blue);
  color: var(--primary-blue);
  font-family: 'Inter', sans-serif;
  line-height: 1.6;
}

/* Glassmorphism for navbar and footer */
.navbar, .footer {
  background: var(--glass-bg);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border: 1px solid var(--glass-border);
  box-shadow: var(--shadow);
  padding: 1rem 2rem;
  position: sticky;
  top: 0; /* For navbar */
  z-index: 1000;
}

.navbar a, .footer a {
  color: var(--primary-blue);
  text-decoration: none;
  margin: 0 1rem;
  transition: color 0.3s ease;
}

.navbar a:hover, .footer a:hover {
  color: var(--white);
}

/* Main content container */
.container {
  background: var(--white);
  border-radius: 12px;
  padding: 2rem;
  margin: 2rem auto;
  max-width: 1200px;
  box-shadow: var(--shadow);
  border: 1px solid var(--glass-border);
}

/* Buttons with hover effect */
button, .btn {
  background-color: var(--primary-blue);
  color: var(--white);
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

button:hover, .btn:hover {
  background-color: #afc8ff;
  transform: translateY(-2px);
}

/* Headings */
h1, h2, h3 {
  color: var(--primary-blue);
  margin-bottom: 1rem;
}

/* Ensure Tailwind compatibility if used */
@tailwind base;
@tailwind components;
@tailwind utilities;